
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
  components: {
    Group,
    Cell,
    Datetime,
    PopupPicker,
    XInput,
    XTextarea,
    Popup,
    Checker,
    CheckerItem,
    XButton
  },
  data () {
    return {
      face: '../../static/img/face.png',
      popupVisible: false,
      // title: '和亲友们来比拼',
      title: '',
      price: ['100元'],
      priceList: [['10元', '50元', '100元', '200元', '500元', '1000元', '2000元', '5000元', '10000元']],
      // text: '为健康我也是拼了，不服来挑战',
      text: '',
      main: true,
      time_dialog: true,
      startDay: '2017-06-29',
      endDay: '2017-06-29',
      day: ['10期'],
      dayList: [['1期', '2期', '3期', '4期', '5期', '6期', '7期', '8期', '9期', '10期']],
      birthday: null,
      type: '自律派',
      showPopup: false,
      loadingButton: true
    }
  },
  methods: {
    setStartDay () {
      this.$vux.datetime.show({
        cancelText: '取消',
        confirmText: '确定',
        format: 'YYYY-MM-DD',
        value: '2017-06-29',
        // 点击确定后的回调
        onConfirm: (val) => {
          // 设置 默认的开始时间
          this.startDay = val
        },
        onShow () {
          // console.log('plugin show')
        },
        onHide () {
          // console.log('plugin hide')
        }
      })
    },
    setEndDay () {
      this.$vux.datetime.show({
        cancelText: '取消',
        confirmText: '确定',
        format: 'YYYY-MM-DD',
        value: '2017-06-29',
        // 点击确定后的回调
        onConfirm: (val) => {
          // 设置 默认的开始时间
          this.endDay = val
        },
        onShow () {
          // console.log('plugin show')
        },
        onHide () {
          // console.log('plugin hide')
        }
      })
    },
    // 获取挑战列表页数据
    fetchData () {
      this.$http.get('/static/js/category.json').then((e) => {
        console.info(this.$store.state.vux.isLoading)
        this.$store.state.vux.isLoading = false
      }, (e) => {
        console.info(e)
      })
    },
    postData () {
      this.loadingButton = false
    }
  },
  created () {
    console.info('challenge.vue')
    document.body.scrollTop = 0

    this.fetchData()
  }
}



